
@import url(./styles/reset.css);
.header {
  overflow: hidden;
  padding: 0 12px;
  background: linear-gradient(90deg, #fe5187, #8782c4, #16d4ac);
  box-shadow: 0 2px 5px 1px rgba(39, 50, 58, 0.3);
  height: 50px;
}
.header .logo {
    float: left;
    margin-right: 20px;
}
.header ul {
    float: left;
    color: #fff;
    cursor: pointer;
    padding: 10px 20px;
    margin-top: 3px;
}
.header ul li {
      float: left;
      padding: 0 20px;
}
.header ul li a {
        display: block;
        color: #fff;
}
.header ul li:not(:first-child) {
        border-left: 1px solid #fff;
}
.header select {
    float: right;
    display: none;
}
.footer .download {
  overflow: hidden;
  padding: 20px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #C7D9E8;
}
.footer .download div {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    width: 300px;
    box-sizing: border-box;
    padding: 0 calc((50vw - 300px)/2);
}
.footer .download div a {
      display: inline-block;
      color: #3d505f;
}
.footer .download div a img.downloadlink {
        width: 300px;
        height: 225px;
}
.footer .download .left {
    border-right: 1px solid #7b8994;
}
.footer .download .left li:not(:first-child) {
      margin-top: 10px;
}
.footer .desc {
  color: #7b8994;
  background: #27323A;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 12px;
}
.footer .desc a {
    color: #7b8994;
}
.main {
  text-align: center;
}



